.App {
  cursor: crosshair;
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.wrapper{
  width:300px;
  height:300px;
  position: relative;
}

.canvas_wrapper{
  width:100%;
  height:100%;
  position: relative;
  overflow: hidden;
}

.rond{
  touch-action: none;
  width:10%;
  height:10%;
  cursor: crosshair;
  border:solid 2px white;
  -webkit-border-radius: 50%; 
  -moz-border-radius: 50%; 
  border-radius: 50%;
  background-color: red;
  position:absolute;
  z-index:10;
  box-shadow: 0px 2px 8px 1px rgba(0,0,0,0.2);
}
.mask{
  position: absolute;
  top:0;
  left:0;
}

.color{
  position: absolute;
  top:-100%;
}

.icon{
  width:100px;
}

.hide{
  display:block;
  cursor: crosshair;
  position:absolute;
  top:-100vh;
  width:24px;
  height:24px;
}

.ValueHSV{
  margin-top:24px;
  position:relative;
  width:300px;
  height:12px;
  background: linear-gradient(to right,black,white);
}

.ValueHSV span {
  width:24px;
  height:24px;
  position:absolute;
  top:-6px;
  border-radius:50%;
  -webkit-border-radius: 50%; 
  -moz-border-radius: 50%; 
  box-shadow: 0px 2px 8px 1px rgba(0,0,0,0.2);
  border:solid 2px rgb(185,116,255) ;
}

canvas{
  width:100%;
  height:100%;
}